@import '_vars'

nav
    details
        position: absolute
        top: 1rem
        right: 1rem
        text-align: right
        width: 100px

        div.link-menu
            a
                font-size: 24px

.radio-fallback
    div.choice
        position: relative

        input[type="radio"]
            position: absolute
            top: 0
            left: 0

            cursor: pointer
            transform: scale(7) translateX(50%) translateY(50%)
            opacity: 0
            filter: blur(5px) brightness(0)
            transition: opacity 0.1s

            &:checked
                opacity: 0.9
                filter: blur(5px) hue-rotate(20deg)

@media (max-width: 336px)
    .radio-fallback
        div.choice
            input[type="radio"]
                transform: scaleX(12) scaleY(7) translateX(80%) translateY(50%)
